@use '_config/variable' as config_variable;
@use '_variable-font-icon/interface' as font-icon--interface;
@use 'variable-frame';
@use 'variable-interact';

#breadcrumb {
    // text-align: center;

    ol {
        padding-inline-start: 0px;
        margin: -0.5 * variable-frame.$frame_unit;
    }

    li {
        list-style: none;
        display: inline-block;

        text-indent: initial;
        padding-left: initial;

        margin: 0px;

        &:not(:first-child) {
            margin-left: variable-interact.$interact_margin;

            &::before {
                // content: '>';
                // font-weight: bold;
                content: font-icon--interface.$font-icon--interface-arrow-down;
                font-family: 'font-icon-interface';
                transform: rotate(-90deg);
                display: inline-block;
                font-size: 0.75em;
                margin-right: variable-interact.$interact_margin;
            }
        }

        &:last-child span {
            @include variable-interact.interact_structure();

            display: inline-block;
            // background-color: config_variable.$CONFIG_CUSTOM_BRAND_COLOR;
            background-color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG_NAV;
            font-weight: bold;
            color: rgba(config_variable.$CONFIG_CUSTOM_SITE_COLOR_TEXT, 0.875);
            // color: black;
            border: 0.125rem solid rgba(config_variable.$CONFIG_CUSTOM_SITE_COLOR_TEXT, 0.25);
            padding-top: 0.125rem;
            padding-bottom: 0.125rem;
        }
    }
}
