@use 'sass:map';
@use '_config/variable' as config_variable;
@use 'variable';
@use 'variable-frame';
@use 'variable-header';
@use 'mixin/media' as mixin_media;

header {
  background-color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG_NAV;
  color: text-color(config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG_NAV);
  text-align: center;

  &, .frame--content {
    padding: variable-frame.$frame_unit;
  }

  & > .frame--content > nav {
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
  }

  #nav_header__toggle {
    line-height: 1;
  }

  #nav_header__brand {
    line-height: 0px;
    max-width: map.get(mixin_media.$breakpoints, 'phone');
    margin-bottom: variable.$unit_root_2;
    border-radius: 1.5 * variable.$unit_root_2;

    img {
      border: none;
      margin: 0px;
    }
  }

  #nav_header__row__button {
    text-align: center;
  }
}
