@use 'sass:list';
@use '_config/variable' as config_variable;
@use '_variable-font-icon/interface' as font-icon--interface;
@use 'variable';
@use 'variable-color';
@use 'variable-details';
@use 'variable-frame';
@use 'variable-header';

// Header:

h1, h2, h3, h4, h5, h6 {
  @include variable-header.header_common();

  color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_LINK;
  font-family: 'roman_font_7';
  letter-spacing: 0.2 * variable.$unit_relative_1;
  background: linear-gradient(180deg, config_variable.$CONFIG_CUSTOM_SITE_COLOR_PRIMARY, config_variable.$CONFIG_CUSTOM_SITE_COLOR_SECONDARY);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  & * {
    background: none;
    -webkit-background-clip: initial;
    -webkit-text-fill-color: initial;
  }

  .counter {
    opacity: 0.875;
    font-family: variable.$page_font;
    letter-spacing: initial;

    &::before {
      background: linear-gradient(180deg, config_variable.$CONFIG_CUSTOM_SITE_COLOR_PRIMARY, config_variable.$CONFIG_CUSTOM_SITE_COLOR_SECONDARY) !important;
    }
  }
  
  & > span.text {
    color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_LINK;
    font-family: 'roman_font_7';
    background: linear-gradient(180deg, config_variable.$CONFIG_CUSTOM_SITE_COLOR_PRIMARY, config_variable.$CONFIG_CUSTOM_SITE_COLOR_SECONDARY);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

h1 {
  font-size: variable-header.$header-fontsize-1;
}

h2 {
  font-size: variable-header.$header-fontsize-2;
}

h3 {
  font-size: variable-header.$header-fontsize-3;
}

h4 {
  font-size: variable-header.$header-fontsize-4;
}

h5 {
  font-size: variable-header.$header-fontsize-5;
}

h6 {
  font-size: variable-header.$header-fontsize-6;
}

// Details & Summary:

summary {
  list-style: none; /* Removes the default marker */
  -webkit-appearance: none; /* Removes WebKit default styles */
  appearance: none; /* Standard removal */

  &::marker {
    content: none; /* Explicitly removes marker in some browsers */
  }

  &::-webkit-details-marker {
    display:none;
  }
}

// Custom marker:
summary::before {
  content: font-icon--interface.$font-icon--interface-arrow-fill-down-circle;
  font-family: 'font-icon-interface';
  display: inline-block;
  font-size: 1em * variable-details.$details-summary-marker-scale;
  width: variable-details.$details-summary-marker-width;
  margin-right: variable-details.$details-summary-marker-margin;
  transform: rotate(-90deg);
  transition: transform 0.15s ease; /* Smooth rotation animation */
}

// Change marker when open:
details[open] > summary::before {
  transform: rotate(0deg);
}

// List:

// Ordered and unordered lists
@mixin generate-nested-styles($type, $levels, $styles) {
  // Base level
  #{$type}:not([type]) {
    list-style-type: list.nth($styles, 1); // First style in the list

    // Generate nested styles dynamically
    @for $i from 2 through $levels {
      #{'&' + str-repeat(' &', $i - 1)} {
        list-style-type: list.nth($styles, (($i - 1) % list.length($styles)) + 1);
      }
    }
  }
}

// Helper function for string repetition
@function str-repeat($string, $times) {
  $result: '';
  @for $i from 1 through $times {
    $result: $result + $string;
  }
  @return $result;
}

ul, ol {
  list-style-position: inside;
  padding-inline-start: 0em;

  & > * {
    margin-top: variable.$unit_root_3;
    margin-bottom: variable.$unit_root_3;
  }
}

// Ordered lists: decimal, upper-alpha, upper-roman, lower-alpha, lower-roman
@include generate-nested-styles('ol', 10, (decimal, upper-alpha, upper-roman, lower-alpha, lower-roman));

// Unordered lists: disc, circle, square
@include generate-nested-styles('ul', 10, (disc, circle, square));

$list_line_width: variable.$unit_relative_0_5;

li {
  text-indent: -1 * variable.$unit_root_3;
  padding-left: variable.$unit_root_3;

  & > * {
    text-indent: initial;
    padding-left: initial;
  }

  & > ol, & > ul {
    position: relative;
    margin-left: -1 * variable.$unit_root_3;

    &:before {
      content: '';
      display: block;
      position: absolute;
      background-color: rgba(config_variable.$CONFIG_CUSTOM_SITE_COLOR_TEXT, 0.125);
      width: $list_line_width;
      top: 0;
      bottom: 0;
      border-radius: $list_line_width * 0.5;
      // left: 0;
    }
  }

  & > ol {
    &:before {
      margin-left: -1 * variable.$unit_root_2;
    }
  }

  & > ul {
    &:before {
      margin-left: (-1 * variable.$unit_root_2);
    }
  }

  & > p {
    display: inline;
  }
}

