// Misc:

:not(pre) > code, pre, time, var, mark, kbd, samp {
  background-color: rgba(config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG, 0.75);
  @include variable-color.color_border_context();
  border-radius: variable.$unit_relative_1 !important;
  padding: variable.$unit_relative_0_125 variable.$unit_relative_1 * 0.75 !important;
  display: inline-block;
  // text-shadow: initial;

  table & {
    white-space: normal;
    overflow-wrap: normal;
    word-break: normal;
    width: min-content;
  }
}

pre {
  overflow-x: auto;
  padding: variable.$unit_relative_1 variable.$unit_relative_2;
  max-width: 100%;
  display: block;
  width: initial;
  width: fit-content;
}

$interact_color_mark: yellow;

mark {
  background-color: $interact_color_mark;
  color: text-color($interact_color_mark);
}

p, hr, blockquote {
  @include variable-frame.style_frame_content_margin();
}

hr {
  margin-top: variable.$unit_root_6;
  margin-bottom: variable.$unit_root_6;
}

:not(li) {
  & > ol, & > ul {
    @include variable-frame.style_frame_content_margin();
  }
}

li {
  & > ol, & > ul {
    @include variable-frame.style_frame_content_margin(0.125);
  }
}

