@use 'variable';
@use 'variable-color';

audio {
  display: block;
  width: 100%;
  border-radius: variable.$unit_root_1;
}

img {
  max-width: 100%;
  // @include variable-color.color_border_context();

  &:not([src$=".svg"]) {
    border-radius: variable.$unit_root_1;
  }
}

svg {
  display: block;
}

video {
  display: block;
  width: 100%;
  border-radius: variable.$unit_root_1;
  @include variable-color.color_border_context();
}

canvas {
  display: block;
  width: 100%;
  border-radius: variable.$unit_root_1;
  @include variable-color.color_border_context();
}
