@use '_config/variable' as config_variable;
@use 'variable';
@use 'variable-frame';

section {
  padding: variable-frame.$frame_unit;
}

section:not(.section--invisible) {
  background-color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG;
  padding: variable.$unit_root_2;
  border-radius: variable.$unit_root_2;

  &:not(:first-child) {
    margin-top: variable.$unit_root_2;
  }

  & > *:first-child {
    margin-top: 0px !important;
  }
}
