@mixin content_spacing( $outer_top, $outer_bottom, $inner_top, $inner_bottom ) {
  & > *:first-child       { margin-top:    $outer_top; }
  & > *:last-child        { margin-bottom: $outer_bottom; }
  & > *:not(:first-child) { margin-top:    $inner_top; }
  & > *:not(:last-child)  { margin-bottom: $inner_bottom; }
}

@mixin content_spacings( $outer, $inner ) {
  @include content_spacing( $outer, $outer, $inner, $inner );
}

@mixin element_center() {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
}
