@use 'sass:list';
@use '_config/variable' as config_variable;
@use 'variable';

html {
  -webkit-overflow-scrolling: touch;
}

body {
  // background-image: url('/asset/image/interface/background.png');
  // background-position: top center;
  // background-repeat: no-repeat;

  background-color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG;
  color: config_variable.$CONFIG_CUSTOM_SITE_COLOR_TEXT;
  margin: 0px;
  font-family: variable.$page_font;
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  // letter-spacing: 0.1rem;
}

@mixin text_shadow($color, $blur: 0.5rem, $count: 3) {
  $shadows: ();
  @for $i from 1 through $count {
    $shadows: list.append($shadows, #{$color} 0rem 0rem #{$blur}, comma);
  }
  text-shadow: $shadows;
  -webkit-text-shadow: $shadows;
}

main {
  background-image: url('/asset/image/interface/background.png');
  // background-position: top center;
  background-position-y: top;
  // background-position-x: 0.15625 * variable.$unit_root_2;
  background-position-x: center;
  background-repeat: repeat-x;
  background-size: 250 * variable.$unit_root_2;
  image-rendering: -webkit-optimize-contrast; /* Chrome, Safari */
  image-rendering: crisp-edges;              /* Firefox */
  image-rendering: pixelated;                 /* Optional for retro styles */

  // @include text_shadow(config_variable.$CONFIG_CUSTOM_SITE_COLOR_BG, 0.5em, 10);
  // @include text_shadow(red, 0.5em, 10);
}
