@use 'sass:map';
@use 'sass:math';
@use 'variable';
@use 'variable-frame';
@use 'mixin/content' as mixin_content;
@use 'mixin/media' as mixin_media;

.row {
  display: block;
  padding: variable.$unit_root_1;
  clear: both; // Fix for sequential row's columns bottom margin interference

  &:before, &:after { display: table; content: " "; }
  &:after { clear: both; }

  & > * {
    padding: variable.$unit_root_1;
  }
  
  // Fix for nested rows having extra padding
  & & {
    // margin: 0px;
    padding: 0px;
  }

  // // Fix for sequential rows having extra space between them
  // section>& + & {
  //   // margin-top: -16px;
  //   padding-top: 0px;
  //   // background: red;
  // }

  section > & {
    margin-left: - variable-frame.$frame_unit;
    margin-right: - variable-frame.$frame_unit;

    &:not(:first-child) {
      margin-top: - variable-frame.$frame_unit;
      padding-top: 0px;
    }

    &:not(:last-child) {
      margin-bottom: - variable-frame.$frame_unit;
    }

    &:first-child {
      margin-top: - variable-frame.$frame_unit;
    }

    &:last-child {
      margin-bottom: - variable-frame.$frame_unit;
    }
  }
}

$columns: 12;

@mixin generate-col-classes($prefix) {
  @for $i from 1 through $columns {
    & > .col-#{$prefix}-#{$i} {
      float: left;
      width: math.div(100%, $columns) * $i;
    }
  }
  & > .col-#{$prefix}-12 {
    width: 100%;
  }
}

@mixin generate-col-classes-auto-clear($prefix) {
  @for $i from 1 through $columns {
    &.row--auto-clear > .col-#{$prefix}-#{$i}:nth-child(n) {
      // background-color: red;
      clear: none;
    }
  }
  &.row--auto-clear > .col-#{$prefix}-6:nth-child(2n + 1) {
    // background-color: yellow;
    clear: both;
  }
  &.row--auto-clear > .col-#{$prefix}-4:nth-child(3n + 1) {
    // background-color: yellow;
    clear: both;
  }
  &.row--auto-clear > .col-#{$prefix}-3:nth-child(4n + 1) {
    // background-color: yellow;
    clear: both;
  }
  &.row--auto-clear > .col-#{$prefix}-2:nth-child(6n + 1) {
    // background-color: yellow;
    clear: both;
  }
  &.row--auto-clear > .col-#{$prefix}-1:nth-child(12n + 1) {
    // background-color: yellow;
    clear: both;
  }
}

.row {
  & > [class*="col-"] {
    position: relative;

    @include mixin_content.content_spacings( 0px, variable.$unit_root_2 );
  }

  // @include generate-col-classes('default');
  
  @each $prefix, $size in mixin_media.$breakpoints {
    // @media (min-width: #{$size}) {
    @include mixin_media.media(">#{map.get(mixin_media.$breakpoints, $prefix)}") {
      @include generate-col-classes($prefix);
      @include generate-col-classes-auto-clear($prefix);
    }
  }
}

.row {
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 0px;
  padding-right: 0px;
  // margin-left: -1 * variable-frame.$frame_unit;
  // margin-right: -1 * variable-frame.$frame_unit;
  // margin-top: -1 * variable-frame.$frame_unit;
  // margin-bottom: -1 * variable-frame.$frame_unit;
}
